.padding-all-1em{
    padding: 1em 1em 1em 1em;
}
.gradient{
    background: -webkit-linear-gradient(45deg, rgb(255, 253, 242), rgb(246, 246, 249));
    background: -o-linear-gradient(45deg, rgb(255, 253, 242), rgb(246, 246, 249));
    background: linear-gradient(45deg, rgb(255, 253, 242), rgb(246, 246, 249));
}


